@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  margin: auto;
  max-width: 1400px;
  min-width: 300px;
  width: 100%;
  height: 100%;
}

* {
  font-family: 'Noto Sans TC', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

h1 {
    color: #0C0639;
    font-weight: 800;
    margin-right: 5px;
}

#info-button {
    -webkit-flex: 0 1 auto;
            flex: 0 1 auto;
    margin-left: auto;
}

.first-page-main {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 480px;
}

.h1-and-icon {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.search-div {
    margin-top: 30px;
    display: -webkit-flex;
    display: flex;
    width: 50%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}


.search-text-field {
    width: 90%;
}

.twitter {
    width: 40px;
    height: 40px;
}

.info-button {
    font-family: 'Open Sans', serif;
}
.twittPage-main {
    background-color:#e3f2fd;
    width: 90%;
    margin: 65px auto 0;
}

#avatar {
    width: 200px;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
}

h1 {
    margin-top: 15px;
    color:#0C0639;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 800;
    text-align: center;
}

h3 {
    text-align: center;
    color: #424242;
    font-weight: 100;
}

blockquote {
    margin-top: 15px;
    text-align: center;
    color: #0C0639;
    
}

.account-info {
    color: #424242;
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1fr;
    gap: 1fr;
    -webkit-justify-content: center;
            justify-content: center;
}

.container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#cal-img {
    width: 20px;
    height: 20px;
    margin-left: 15px;
}

hr {
    background-color: #42a5f5;
    opacity: 0.5;
    margin-top: 30px;
}

.statistics {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.statistics-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

@media screen and (max-width: 500px) {
    .account-info {
        grid-template-columns: 1fr;
    }
    .statistics {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}
